import React,{Component} from 'react';
import {Nav, Navbar, NavDropdown} from 'react-bootstrap';
import styled from 'styled-components';

/* 
      HEx
$color1: #e63946ff;
$color2: #f1faeeff;
$color3: #a8dadcff;
$color4: #457b9dff;
$color5: #1d3557ff;
*/


const NavStyles = styled.div `

*{
    margin: 0px;
    padding: 0px;
    border-sizing: border-box;
    font-family: 'Lato', sans-serif;
    
}

.navbar{
     background-color:rgba(254,150,0,0.4);
  }
  
   .navbar-nav .nav-link {
    color: white;
    font-weight:20px;
    margin:10px 20px 10px 20px;

    &:hover {
      color :#457BD9;
    }
    &:active{
      
    }

  }

   a, .navbar-brand{
      color:#1D3557;
      font-size: 20px;
      margin-left:10px;


      &:hover {
        color: #ACB8D2;
      }
  }

.navbar-toggler:focus,
.navbar-toggler:active,
.navbar-toggler-icon:focus {
    outline-color: white;
}

.dropdown-menu, .dropdown-item{
    background-color: rgba(254,150,0,0.2);
    margin:auto;

    &:hover{
        color: #ACB8D2
    }
}

.dropdown-item{
    text-align:center;
    border:none;
}
`;


export default class Navigation extends Component{
    render(){
    return (
            <NavStyles>
                <Navbar expand = "lg">
                <Navbar.Brand href = "/"> 
                    Ethan Meitz
                </Navbar.Brand> 
                    <Navbar.Toggle aria-controls='basic-navbar-nav' />
                        <Navbar.Collapse id = "basic-navbar-nav">
                            <Nav  className = "ml-auto">
                                    <Nav.Link href="/">HOME</Nav.Link>              
                                    <Nav.Link href="/art">ART</Nav.Link>            
                                    <NavDropdown title = "PROJECTS" >
                                        <NavDropdown.Item href = "/projects/blackfly">Project 1</NavDropdown.Item>
                                        <NavDropdown.Item href = "/projects/project2">Project 2</NavDropdown.Item>
                                        <NavDropdown.Item href = "/projects/project3">Project 3</NavDropdown.Item>
                                        <NavDropdown.Item href = "/projects">All Projects</NavDropdown.Item>
                                    </NavDropdown>  
                                    <Nav.Link href="/fun">OTHER</Nav.Link>
                                    <Nav.Link href="/contact">CONTACT</Nav.Link>    
                            </Nav>
                        </Navbar.Collapse>
                    </Navbar>  
            </NavStyles>
            )
    }
}

