import React, {Component} from 'react';
import styled from 'styled-components';

import Navigation from './nav-normal';



const FunStyles = styled.div `

.p5Canvas{
    margin:0;
}

.grid{
    display:grid;
    width: 80%;
    grid-template-columns: 40% 40%;
    grid-gap:20px;
    border: 3px solid blue;
    margin:auto;
    justify-content:space-around;
}
.animation{
    
    width:500px;
    height:500px;
}

iframe{
    width: 500px;
    height: 500px;
    overflow:hidden;
}

`;



export default class my_projects extends Component {

    render(){
        return (
    <>
        <Navigation />
        <FunStyles>
            
            <main className = "grid">
                <div className = "animation">
                    <iframe  title = "bouncing-balls" scrolling="no" frameborder="0" src="https://editor.p5js.org/ejmeitz/embed/Ggf2mFF9o"></iframe>
               </div>
               <div className = "animation">
                    <iframe  title = "bouncing-balls" scrolling="no" frameborder="0" src="https://editor.p5js.org/ejmeitz/embed/Ggf2mFF9o"></iframe>
                </div>
                <div className = "animation">
                    <iframe  title = "bouncing-balls" scrolling="no" frameborder="0" src="https://editor.p5js.org/ejmeitz/embed/Ggf2mFF9o"></iframe>
                
                </div>
                <div className = "animation">
                    <iframe  title = "bouncing-balls" scrolling="no" frameborder="0" src="https://editor.p5js.org/ejmeitz/embed/Ggf2mFF9o"></iframe>
                </div>
            </main>
        </FunStyles>
    </>
        )
    }
}