import React, {Component} from 'react';
import styled from 'styled-components';
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';
import {faGithub } from '@fortawesome/free-brands-svg-icons';
import "../index.css";

import Navigation from './nav-normal';


const ProjectStyles = styled.div `
    .grid {
        display:grid;

        grid-template-columns: 40% 40%;
        grid-gap: 20px;
        margin: 10px 50px 0px 50px;


        grid-auto-flow:rows;
        grid-auto-rows:500px
       
        justify-items:center;
        justify-content:center;
        align-items:stretch;
        
    }

    .element{
        border-radius:3px;
        background-color:#F8F8F8;
        height: 60vh;
        box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.8);
         
    }
    .ProjectName{
        text-align:center;
       
    }
    .Info{
        font-family: 'Lato', sans-serif;
    }
    


`;


export default class my_projects extends Component {

    render(){
        return (
    <>
        <Navigation />
            <ProjectStyles>
                <h1>
                    Projects Page
                </h1>
                <div className = "grid">
                    <div className = "element a">
                        <img src = ""/>
                        <h2 className = "ProjectName">
                            Blackfly Polarization Camera GUI
                        </h2>
                        <div className = "Info">
                            <h3><a href ="http://localhost:3000/projects/blackfly">About:</a> </h3>       
                            <h3>Languages: C++</h3>
                                <a 
                                href = "https://github.com/ejmeitz/GUI"
                                className = "gitHub social" 
                                target = "_blank"
                                rel="noopener noreferrer">
                                <FontAwesomeIcon icon = {faGithub} size = "2x" />
                                </a>
                        </div>
                    </div>

                    <div className = "element b">
                        <img src = ""/>
                        <h2 className = "ProjectName">
                            My Website
                        </h2>
                        <div className = "Info">
                            <h3><a href ="http://localhost:3000/projects/project2" target = "_blank" rel="noopener noreferrer">About:</a> </h3>       
                            <h3>Languages: JS, CSS, HTML </h3>
                            <a 
                            href = "https://github.com/ejmeitz/My-Website"
                            className = "gitHub social" 
                            target = "_blank"
                            rel="noopener noreferrer">
                            <FontAwesomeIcon icon = {faGithub} size = "2x" />
                            </a>
                        </div>
                    </div>

                    <div className = "element c">
                        <img src = ""/>
                        <h2 className = "ProjectName">

                        </h2>
                        <div className = "Info">
                            <h3>About: </h3>       
                            <h3>Languages:</h3>
                        </div>
                    </div>
                </div>
            </ProjectStyles>
                
    </>
        )
    }
}