import React, {Component} from 'react';
import styled from 'styled-components';
import NavHome from './nav-home';


import '../index.css';
 

const HomeStyles = styled.div `

*{
    margin:0px;
    padding:0px;
    
    box-sizing:border-box;
}

    .welcome{
        color:white;
        font-size: 50px;
        font-weight: 500;
        width: 800px;
        position:relative;
        top:200px;
        left:270px;

        
        
    }
    
    .about{
        width: 490px;
        font-size: 20px;
    }

    @media screen and (max-width: 1000px) {
        .welcome
        {
            left:280px;
            right:auto
            top:auto;
            bottom:auto;
            
        }
    }

.secondPage{
    height: 100vh;
    background-color:rgba(249, 193, 131, 1);
}

    @media screen and (max-width: 500px) {
        .welcome
        {
            left:100px;
            right:100px;
            top:auto;
            bottom:auto;
            
        }
    }

`;

export default class Home extends Component {




render(){
    return (
    <HomeStyles>
        <div className="bg">
            
            <NavHome />
            <div className = "welcome">
                <h1 >
                Welcome to Ethan's Website!
                </h1>
                <p className = "about">
                    <h5>
                    I've posted my art, projects and some fun animations for you to enjoy. Take a look around. 
                    </h5>
                </p>
                
            </div>
            
            
        </div>
        <div className = "secondPage">
            <p>
                This is the second page!
            </p>

        </div>
       
    </HomeStyles>  
    )
}

}