import React,{Component} from 'react';
import {Nav, Navbar, NavDropdown} from 'react-bootstrap';
import styled from 'styled-components';

/* 
      HEx
$color1: #e63946ff;
$color2: #f1faeeff;
$color3: #a8dadcff;
$color4: #457b9dff;
$color5: #1d3557ff;
*/


const NavStyles = styled.div `

*{
    margin: 0px;
    padding: 0px;
    border-sizing: border-box;
    font-family: 'Lato', sans-serif;
    
}


.navbar{
    background-color:rgba(254,150,0,0.4);
    width: 100%;
    padding: 10px;
    position: fixed;
    top: 0;
    transition: top 0.4s;
  }
  
  .navbar--hidden {
    top: -75px;
  }

   .navbar-nav .nav-link {
    color: white;
    font-weight:20px;
    margin:10px 20px 10px 20px;

    &:hover {
      color: #457BD9;
    }
    &:active{
      color: #457BD9;
    }

  }

   a, .navbar-brand{
      color:white;
      font-size: 20px;
      margin-left:10px;


      &:hover {
        color: white;
      }
  }

  #nav-dropdown{
    color: white;
    font-weight:20px;
    margin:10px 20px 10px 20px;

    &:hover {
      color: #457BD9;
    }
    &:active{
      color: #457BD9;
    }

  }

   a, .navbar-brand{
      color:white;
      font-size: 20px;
      margin-left:10px;


      &:hover {
        color: white;
      }
  }

.navbar-toggler{
  margin-right:20px;
}
.navbar-toggler:focus,
.navbar-toggler:active,
.navbar-toggler-icon:focus {
    outline-color: black;
}


.dropdown-menu, .dropdown-item{
    background-color: rgba(249, 193, 131, 1);
   
    margin:auto;

    &:hover{
        color: #457BD9
    }
}

.dropdown-item{
    text-align:center;
    border:none;
}


`;


export default class NavHome extends Component{
    
    constructor(props) {
        super(props);
    
        this.state = {
          prevScrollpos: window.pageYOffset,
          visible: true
        };
      }
    
      // Adds an event listener when the component is mount.
      componentDidMount() {
        window.addEventListener("scroll", this.handleScroll);
      }
    
      // Remove the event listener when the component is unmount.
      componentWillUnmount() {
        window.removeEventListener("scroll", this.handleScroll);
      }
    
      // Hide or show the menu.
      handleScroll = () => {
        const { prevScrollpos } = this.state;
    
        const currentScrollPos = window.pageYOffset;
        const visible = prevScrollpos > currentScrollPos;
    
        this.setState({
          prevScrollpos: currentScrollPos,
          visible
        });
      };
    
    
    
    render(){
    return (
            <NavStyles>
                <Navbar expand = "lg" className={"navbar", {
          "navbar--hidden": !this.state.visible}}>
                <Navbar.Brand href = "/"> 
                    Ethan Meitz
                </Navbar.Brand> 
                    <Navbar.Toggle aria-controls='basic-navbar-nav' />
                        <Navbar.Collapse id = "basic-navbar-nav">
                            <Nav  className = "ml-auto">
                                    <Nav.Link href="/">HOME</Nav.Link>              
                                    <Nav.Link href="/art">ART</Nav.Link>            
                                    <NavDropdown title = "PROJECTS" id = "nav-dropdown">
                                        <NavDropdown.Item href = "/projects/blackfly">Project 1</NavDropdown.Item>
                                        <NavDropdown.Item href = "/projects/project2">Project 2</NavDropdown.Item>
                                        <NavDropdown.Item href = "/projects/project3">Project 3</NavDropdown.Item>
                                        <NavDropdown.Item href = "/projects">All Projects</NavDropdown.Item>
                                    </NavDropdown>  
                                    <Nav.Link href="/fun">OTHER</Nav.Link>
                                    <Nav.Link href="/contact">CONTACT</Nav.Link>    
                            </Nav>
                        </Navbar.Collapse>
                    </Navbar>  
            </NavStyles>
            )
    }
}

